.custom-menu-wrapper {
	background: white;
	width: 88vw;
	justify-content: center;
	margin: auto;
	margin-left: 5.5em;
}

.custom-navbar {
	margin-left: 20px;
	margin-top: -1px;
	padding-top: 10px;
}

.custom-title {
	font-family: 'aktiv_groteskbold';
	text-align: start;
	margin-top: 31px;
	margin-left: 30px;
}

.custom-link {
	color: #717171;
	font-family: 'aktiv_groteskmedium';
	font-size: 14px;
}

.custom a {
	text-decoration: none;
}

.custom-link:focus {
	border-style: none;
}

.custom-link:hover {
	color: #1498be !important;
	border-style: none;
}

.custom-link.active {
	color: #1498be !important;
	border-style: none;
	border-bottom: 3px solid #1498be !important;
}

.custom-link:active {
	border-style: none;
}

.people-title {
	font-family: 'aktiv_groteskbold';
	font-size: 21px;
	color: #000000;
	text-align: start;
	padding-top: 20px;
	padding-left: 20px;
}

.people-subtext {
	font-size: 15px;
	color: #000000;
	text-align: start;
	padding-left: 20px;
}

#slug-text {
	font-size: 13px;
	position: absolute;
	margin-top: -26px;
	margin-left: 55px;
}

label.people-label {
	float: left !important;
	color: #000000;
	padding-left: 20px;
	font-size: 15px;
}

.people-input {
	width: 520px !important;
	margin-top: 15px;
	height: 43px;
	margin-left: 20px;
}

#fieldType {
	width: 170px !important;
	margin-top: 15px;
	margin-left: 20px;
}

.form-group br {
	background-color: #1498be !important;
}

.custom-btn {
	background-color: #60bad5;
}

.custom-btn-div {
	margin-left: 20px;
}

table {
	text-align: left;
}

.table-hover tbody tr:hover {
	background-color: #fbfdfe;
}

.edit-btn {
	color: #1498be;
	font-family: 'aktiv_groteskbold';
	font-size: 11px;
	width: 0.2em;
	padding-right: 0px;
	padding-top: 15px !important;
}

.table-header {
	font-family: 'aktiv_groteskmedium';
	font-size: 14px;
	color: #8f8f8f;
}

.col1 {
	width: 0.2em;
}

.col2 {
	padding-left: 0px !important;
}

.table-header {
	border-top: none !important;
	border-bottom: 1px solid #dee2e6 !important;
}

.del-btn {
	width: 0.2em !important;
}

a.btn.btn-default.actionButton {
	padding-top: 0px !important;
}

/* Media Queries */
@media screen and (max-width: 960px) {
	.people-input {
		width: 350px !important;
		margin-top: 15px;
		height: 43px;
		margin-left: 20px;
	}

	#fieldType {
		width: 170px !important;
		margin-top: 15px;
		margin-left: 20px;
	}

	#slug-text {
		font-size: 13px;
		position: absolute;
		margin-top: -26px;
		margin-left: -45px;
	}
	.custom-navbar {
		margin-left: 10px;
	}
}
